var levelsArr = [];
for (var i = 0; i <= 10.0; i++) {
    levelsArr[i] = i / 2;
}

export const levels = levelsArr;


function toTime(hour) {
    var res;
    if (hour % 1 == 0)
        res = ":00";
    else
        res = ":30";
    if (hour < 10)
        res = "0" + parseInt(hour) + res;
    else
        res = parseInt(hour) + res;
    return res;
}

var hoursArr = [];
for (var j = 0; j <= 48; j++) {
    hoursArr[j] = toTime(j / 2);
}

export const hours = hoursArr;